<template>
  <div>
    <div class="main">
      <div class="img">
        <img :src="$pre+msg.img" alt />
      </div>
      <div class="con">
        <h3>{{msg.goodsname}}</h3>
        <span class="pri">&yen;{{msg.price}}</span>
        <button v-if="msg.ishot == 1">热卖</button>
        <button v-if="msg.isnew == 1">新品</button>
        <br />
        <span class="del">&yen;{{msg.market_price}}</span>
        <hr />
        <div v-html="msg.description"></div>
      </div>
    </div>
    <div class="footer">
      <el-button type="primary" @click="show = true">加入购物车</el-button>
    </div>
    <!-- <van-cell is-link @click="showPopup">展示弹出层</van-cell> -->
    <van-popup
      v-model="show"
      closeable
      close-icon="close"
      position="bottom"
      :style="{ height: '30%' }"
    >
      <div class="clear">
        <div class="pic">
          <img :src="$pre+msg.img" alt />
        </div>
        <h3>{{msg.goodsname}}</h3>
      </div>
      <h2>{{msg.specsname}}</h2>
      <div class="nav">
        <span
          v-for="(item,index) in msg.specsattr"
          :key="item"
          :class="[index===n?'select':'']"
          @click="n=index"
        >{{item}}</span>
      </div>
      <br />
      <el-button type="primary" @click="shop">加入购物车</el-button>
    </van-popup>
  </div>
</template>

<script>
// reqGetgoodsinfo
import { reqGetgoodsinfo, reqCartadd } from "../../utils/http";
import { Toast } from "vant";
export default {
  methods: {
    showPopup() {
      this.show = true;
    },
    shop() {
      reqCartadd({
        uid: JSON.parse(localStorage.getItem("user")).uid,
        goodsid: this.msg.id,
        num: 1
      }).then(res => {
        Toast(res.data.msg);
        this.show = false;
      });
    }
  },
  data() {
    return {
      msg: {},
      show: false,
      n: 0
    };
  },
  mounted() {
    reqGetgoodsinfo(this.$route.query.id).then(res => {
      this.msg = res.data.list[0];
      this.msg.specsattr = JSON.parse(this.msg.specsattr);
      console.log(this.msg);
    });
  }
};
</script>

<style scoped lang="stylus">
@import '../../stylus/index.styl';

.main {
  padding-bottom: 0.78rem;
  img{
    width 100vw
    height 5rem
  }
}
.con{
 
  h3{
    font-size .4rem
    line-height .5rem
    margin-bottom .3rem
    margin-left .3rem
  }
  .pri{
    display inline-block
    color red
    margin .3rem
    margin-bottom .1rem
  }
  .del{
    margin-left .2rem
    text-decoration line-through
    display inline-block
    margin-bottom .3rem
  }

}

.footer {
  height: $footer;
  border-top: 1px solid #ff4400;
  position: fixed;
  bottom: 0;
  width: 100vw;
  background: $font-color5;
  overflow: hidden;

  button {
    height: 100%;
    float: right;
  }
}

.clear {
  overflow: hidden;
  padding-top: 0.2rem;

  h3 {
    line-height: $h6;
    font-size: $h2;
  }
}

.pic {
  float: left;
  height: $homeimg;
  width: $homeimg;
  margin-right: $h1;

  img {
    width: 100%;
    height: 100%;
  }
}

.nav {
  overflow: hidden;
}

.nav span {
  float: left;
  padding: 3px 10px;
  border: 1px solid #333;
  margin: 10px;
}

.nav .select {
  background: $primary;
  color: #ffffff;
  border-color: $primary;
}

h2 {

  font-size: $h2;
  margin-left: 0.2rem;
}
</style>


